import { NavLink } from "react-router-dom";
import { Button, Drawer } from 'antd';
import { useState } from 'react';
import {
    MobileOutlined,
    MailOutlined,
    FacebookFilled,
    TwitterSquareFilled,
    InstagramFilled,
    UserOutlined,
    MenuOutlined,
} from '@ant-design/icons';

function AppHeader() {
    const [open, setOpen] = useState(false);
    const showDrawer = () => {
      setOpen(true);
    };
    const onClose = () => {
      setOpen(false);
    };
    return (
        <div className="container">
            {/* topbar */}
            <div className="topBar">
                <div className="contactInfo">
                    <ul>
                        <li>
                            <a href="tel:03451279940"><MobileOutlined /><span>03451279940</span></a>
                        </li>
                        <li>
                            <a href="mailto:contact@grocery.co.uk"><MailOutlined /><span>contact@grocery.co.uk</span></a>
                        </li>
                    </ul>
                </div>
                <div className='otherInfo'>
                    <ul className='socialMedia'>
                        <li><a href='https://www.facebook.com'><FacebookFilled /></a></li>
                        <li><a href='https:www.instagram.com'><InstagramFilled /></a></li>
                        <li><a href='https:www.twitter.com'><TwitterSquareFilled /></a></li>
                    </ul>
                    <button><UserOutlined /> My Account</button>
                </div>
            </div>
            {/* header */}
            <div className='header separator'>
                <div className='logo'>Grocery</div>
                <div className="mobileVisible">
                <Button type="primary" onClick={showDrawer}>
                <MenuOutlined />
                </Button>
                <Drawer title="Menu" placement="right" onClose={onClose} open={open} >
                <nav>
                    <ul>
                        <li><NavLink onClick={onClose} to='/'>Home</NavLink></li>
                        <li><NavLink onClick={onClose} to='/about'>About</NavLink></li>
                        <li><NavLink onClick={onClose} to='/shop'>Shop</NavLink></li>
                        <li><NavLink onClick={onClose} to='/faq'>Faq</NavLink></li>
                        <li><NavLink onClick={onClose} to='/contact'>Contact</NavLink></li>
                    </ul>
                </nav>
                </Drawer>
                </div>
                <nav className="mobileHidden">
                    <ul>
                        <li><NavLink to='/'>Home</NavLink></li>
                        <li><NavLink to='/about'>About</NavLink></li>
                        <li><NavLink to='/shop'>Shop</NavLink></li>
                        <li><NavLink to='/faq'>Faq</NavLink></li>
                        <li><NavLink to='/contact'>Contact</NavLink></li>
                    </ul>
                </nav>
            </div>
        </div>
    );
}

export default AppHeader;